<template>
  <div>
    <Header />
    <main class="main-content">
      <!--  -->
      <section class="hero">
        <div class="hero-content">
          <h1 class="hero-title">IT</h1>
          <p class="hero-description"></p>
          <div class="hero-image-wrap">
            <img class="hero-image" :src="require('@/assets/images/menu/hero.jpg')" alt="hero image">
          </div>
          <a class="cta-button" @click="goToIntroducePage"></a>
        </div>
      </section>

      <!--  -->
      <div class="user">
        <h1 class="user-message">{{ user.name }}!</h1>
      </div>

      <!--  -->
      <section class="flow">
        <h2 class="section-title"></h2>
        <ul class="flow-list">
          <li class="flow-item"><a class="flow-link" @click="goToHowToPage"></a></li>
          <li class="flow-item"><a class="flow-link" @click="goToSetUPPage"></a></li>
          <li class="flow-item"><a class="flow-link" @click="goToEnvironmentPage"></a></li>
          <li class="flow-item"><a class="flow-link" @click="goToProcedurePage"></a></li>
        </ul>
      </section>

      <!--  (Swiper) -->
      <section class="courses">
        <h2 class="section-title"></h2>
        <swiper :slides-per-view="slidesPerView" :space-between="30" loop>
          <swiper-slide v-for="course in courses" :key="course.id" @click="navigateTo(course.route)">
            <div class="course-card">
              <img :src="require(`@/assets/images/menu/${course.image}`)" :alt="course.title" class="course-image">
              <h3 class="course-title">{{ course.title }}</h3>
              <p class="course-description">{{ course.description }}</p>
            </div>
          </swiper-slide>
        </swiper>
        <div class="button-container">
          <button class="view-all-button" @click="goToCourseMenuPage"></button>
        </div>
      </section>
    </main>
    <Footer />
  </div>
</template>

<script>
import Header from "@/components/layout/Header.vue";
import Footer from "@/components/layout/Footer.vue";
import { Swiper, SwiperSlide } from 'swiper/vue';
import 'swiper/swiper-bundle.css'; // Swiper
import { computed, onMounted, ref } from 'vue';
import { useStore } from 'vuex';
import { useRouter } from 'vue-router';

export default {
  name: "MenuPage",
  components: {
    Header,
    Footer,
    Swiper,
    SwiperSlide,
  },
  setup() {
    const store = useStore();
    const router = useRouter();
    const slidesPerView = ref(3); // 3

    // 
    const updateSlidesPerView = () => {
      const width = window.innerWidth;
      if (width <= 425) {
        slidesPerView.value = 1;
      } else if (width <= 834) {
        slidesPerView.value = 2;
      } else {
        slidesPerView.value = 3;
      }
    };

    // 
    onMounted(() => {
      updateSlidesPerView();
      window.addEventListener('resize', updateSlidesPerView);
    });

    const courses = [
      {
        id: 1,
        title: "HTML & CSS",
        description: "",
        image: "html.png",
        route: "HtmlMenuPage",
      },
      {
        id: 2,
        title: "",
        description: "PC",
        image: "command-line.jpg",
        route: "CommandLineMenuPage",
      },
      {
        id: 3,
        title: "JavaScript",
        description: "",
        image: "javascript.png",
        route: "JavascriptMenuPage",
      },
      {
        id: 4,
        title: "Jquery",
        description: "JavaScript",
        image: "jquery.png",
        route: "JqueryMenuPage",
      },
      {
        id: 5,
        title: "Git",
        description: "",
        image: "git.png",
        route: "GitMenuPage",
      },
      {
        id: 6,
        title: "SQL",
        description: "",
        image: "sql.png",
        route: "SqlMenuPage",
      },
    ];

    const goToIntroducePage = () => {
      router.push({ name: "Introduce" });
    };

    const goToSetUPPage = () => {
      router.push({ name: "Setup" });
    };

    const goToHowToPage = () => {
      router.push({ name: "HowTo" });
    };

    const goToEnvironmentPage = () => {
      router.push({ name: "Environment" });
    };

    const goToProcedurePage = () => {
      router.push({ name: "Procedure" });
    };

    const goToCourseMenuPage = () => {
      router.push({ name: "CourseMenu" });
    };

    const navigateTo = (route) => {
      router.push({ name: route });
    };

    return {
      courses,
      navigateTo,
      goToIntroducePage,
      goToSetUPPage,
      goToHowToPage,
      goToEnvironmentPage,
      goToProcedurePage,
      goToCourseMenuPage,
      slidesPerView,
      user: computed(() => store.state.user.loginData ? store.state.user.loginData : ''),
    };
  },
};
</script>

<style scoped>
/* Main layout */
.main-content {
  font-family: "Helvetica Neue", Arial, sans-serif;
  background-color: #f5f5f5;
}

/* Hero Section */
.hero {
  background: linear-gradient(135deg, #1d1d1d, #4188b6);
  color: #fff;
  text-align: center;
  padding: 100px 20px;
}

.hero-title {
  font-size: 48px;
  margin-bottom: 20px;
}

.hero-description {
  font-size: 20px;
  margin-bottom: 30px;
}

.hero-image-wrap {
  text-align: center;
}

.hero-image {
  object-fit: cover;
  object-position: 50% 20%;
  height: 400px;
  width: 80%;
  margin: 0 auto 40px;
}

.cta-button {
  display: inline-block;
  padding: 15px 30px;
  background-color: #ff9800;
  color: white;
  font-size: 18px;
  text-decoration: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.cta-button:hover {
  background-color: #e57c00;
}

/* User Message */
.user-message {
  font-size: 24px;
  color: #4188b6;
  text-align: center;
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
  margin: 20px auto;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

/* Flow Section */
.flow {
  margin: 0 auto;
  padding: 50px;
  background-color: #fff;
  max-width: 1200px; /*  */
  text-align: center;
}

.flow-list {
  display: flex;
  flex-direction: column;
  align-items: flex-start; /*  */
  margin: 0 auto;
  padding: 0;
  list-style-type: none; /*  */
}

.flow-item {
  width: 100%;
  padding: 15px 0; /*  */
  font-size: 18px;
  font-weight: 500;
  border-bottom: 1px solid #ddd; /*  */
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.flow-item:hover {
  background-color: #f9f9f9; /*  */
}

.flow-link {
  color: #4188b6;
  font-size: 18px;
  text-decoration: none;
  display: inline-block;
  padding-bottom: 2px;
  border-bottom: 2px solid transparent;
  transition: color 0.3s ease, border-bottom 0.3s ease;
}

.flow-link:hover {
  color: #ff9800; /*  */
  border-bottom: 2px solid #ff9800; /*  */
}

/* Courses Section */
.courses {
  padding: 60px 20px;
  background-color: #f9f9f9;
}

.section-title {
  text-align: center;
  font-size: 36px;
  margin-bottom: 40px;
}

.course-card {
  background: #fff;
  border-radius: 10px;
  padding: 20px;
  width: 300px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  margin: 0 10px;
  transition: transform 0.3s ease;
}

.course-card:hover {
  transform: scale(1.05); /*  */
}

.course-image {
  width: 150px;
  height: 150px;
  object-fit: contain;
  margin-bottom: 20px;
}

.button-container {
  text-align: center;
  margin-top: 30px;
}

.view-all-button {
  padding: 10px 20px;
  background-color: #4188b6;
  color: #fff;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.view-all-button:hover {
  background-color: #2a6b93; /*  */
}

/* Swiper Carousel Style */
.swiper-container {
  padding: 30px 0;
}

.swiper-slide {
  display: flex;
  justify-content: center;
}

/* Media Queries */
@media (max-width: 768px) {
  .hero-image {
    height: 300px;
  }
}

/* SP */
@media (max-width: 425px) {
  /* Hero Section */
  .hero-title {
    font-size: 22px; /*  */
  }

  .hero-image {
    height: 150px;
  }

  .hero-description {
    font-size: 16px; /*  */
  }

  .cta-button {
    font-size: 16px; /*  */
  }

  /* User Message */
  .user-message {
    font-size: 20px; /*  */
  }

  /* Flow Section */
  .flow-item {
    font-size: 16px; /*  */
  }

  .flow-link {
    font-size: 13px; /*  */
  }

  /* Courses Section */
  .section-title {
    font-size: 28px; /*  */
  }

  .view-all-button {
    width: 90%; /*  */
  }
}
</style>
